.fondo {
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .7);
    z-index: 100;
  }
 
 .modal {
  position: relative;
     background-color: #311D3F;
     background-color: var(--modalGlass);
     display: flex;
     align-items: center;
     justify-content: center;
     flex-direction: column;
     position: fixed;
     padding: 20px;
     box-shadow: 0px 9px 17px 6px rgba(0,0,0,0.45);
     border-radius: 10px;
     z-index:  1000 !important;
 }


 .openFondo{
  animation: openFondo .5s cubic-bezier(0.22, 1, 0.36, 1) 0s 1 normal none;
}
 .slideIn{
    animation: slideIn .5s cubic-bezier(0.22, 1, 0.36, 1) 0s 1 normal none;
  }
  .CloseModal{
    animation: CloseModal .5s cubic-bezier(0.22, 1, 0.36, 1) 0s 1 normal none;
  }

  .CloseModalFondo{
    animation: CloseModalFondo .5s cubic-bezier(0.22, 1, 0.36, 1) 0s 1 normal none;
  }
 

  @keyframes slideIn {
    0% {
      transform: translateY(-3rem);
      opacity: 0;
    }
  
    100% {
      transform: translateY(0);
      opacity: 1;
    }
  }


  @keyframes CloseModal {
    100% {
      transform: translateY(-3rem);
      opacity: 0;
    }
  
    0% {
      transform: translateY(0);
      opacity: 1;
    }
  }



  @keyframes openFondo {
    0% {
      
      opacity: 0;
    }
  
    100% {
    
      opacity: 1;
    }
  }


  @keyframes CloseModalFondo {
    100% {
      opacity: 0;
    }
  
    0% {
      opacity: 1;
    }
  }

 @media (max-width: 400px) { 

    .modal{
        width: 85vw;
        padding: 10px;
    }
}



