    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    height: 85%;
    width: 70%;
    background-image: url(/minesweeper-deploy/static/media/profileBackground.6a41e1fa.svg);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0px 10px 20px 10px rgba(0, 0, 0, 0.3);
}
.profile-container {
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: .2fr 1.2fr .2fr;
    grid-gap: 0px 0px;
    gap: 0px 0px;
    grid-template-areas:
      "header"
      "savedGame "
      "buttons";
  }
.profile-container-header{
      color:white;
      grid-area:header;
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
  }

.profile-buttons{ 
    grid-area: buttons;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}
.profile-name{
    display: flex;
    align-items: center;
    color :var(--cream)
}
.iconProfile{
    margin: 0px 10px ;
    height: 40px;
    width: 40px;
}
.profile-container-savedGame{
    width: 100%;
    height: 100%;
    grid-area: savedGame;
}


